import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import Emoji from 'a11y-react-emoji';
import SocialIcons from './SocialIcons';

import './footer.scss';

export default function Footer({ site }) {
  return (
    <footer className="footer">
      <SocialIcons social={site.siteMetadata.social} />
      <div>
        <Link to="/blog/making-this-site/" className="built-with">
          built with{' '}
          <Emoji symbol="a ⌨️" className="built-with--keyboard-emoji" />
          <Emoji symbol="❤️" className="built-with--heart-emoji" />
        </Link>{' '}
        | &copy; 2022 {site.siteMetadata.title}
      </div>
    </footer>
  );
}

Footer.propTypes = {
  site: PropTypes.object.isRequired,
};
