import React from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';

export default function Seo({ pageMeta, children }) {
  const { site } = useStaticQuery(graphql`
    query SeoQuery {
      site {
        siteMetadata {
          title
          description
          siteUrl
          image
          social {
            linkedinUrl
            githubUrl
          }
        }
      }
    }
  `);
  const metadata = site.siteMetadata;

  const siteName = metadata.title;
  const title = pageMeta.title ? `${pageMeta.title} | ${siteName}` : siteName;
  const description = pageMeta.description
    ? pageMeta.description
    : metadata.description;
  const url = pageMeta.uri
    ? `${metadata.siteUrl}/${pageMeta.uri}/`
    : metadata.siteUrl;
  const image = pageMeta.image ? pageMeta.image : metadata.image;

  const schema = {
    '@context': 'http://schema.org/',
    '@graph': [
      {
        '@type': 'WebSite',
        name: siteName,
        url: metadata.siteUrl,
      },
      {
        '@type': 'Person',
        name: siteName,
        url: metadata.siteUrl,
        sameAs: [metadata.social.linkedinUrl, metadata.social.githubUrl],
        jobTitle: 'Web Developer',
      },
    ],
  };
  const schemaString = JSON.stringify(schema);

  return (
    <Helmet>
      <html lang="en-US" dir="ltr" />
      <meta charset="utf-8" />
      <meta name="viewport" content="width=device-width, initial-scale=1" />

      <title>{title}</title>
      <meta name="description" content={description} />
      <meta name="author" content={siteName} />
      <link rel="canonical" href={url} />
      <meta name="robots" content="index, follow" />
      <meta name="color-scheme" content="dark light" />
      <link rel="icon" sizes="192x192" href="/favicon.png" />
      <link rel="icon" type="image/svg+xml" href="/favicon.svg" />

      <meta property="og:locale" content="en_US" />
      <meta property="og:type" content="website" />
      <meta property="og:site_name" content={siteName} />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:image" content={image} />
      <meta property="og:url" content={url} />

      <meta name="twitter:card" content="summary" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={image} />
      <meta name="twitter:url" content={url} />

      <script type="application/ld+json">{schemaString}</script>
      {children}
    </Helmet>
  );
}

Seo.defaultProps = {
  pageMeta: {
    title: '',
    description: '',
    uri: '',
    image: '',
  },
  children: [],
};

Seo.propTypes = {
  pageMeta: PropTypes.shape({
    title: PropTypes.string,
    description: PropTypes.string,
    uri: PropTypes.string,
    image: PropTypes.string,
  }),
  children: PropTypes.node,
};
