import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';

export function ExternalLink({ to, children, ...rest }) {
  return (
    <a href={to} target="_blank" rel="noopener noreferrer" {...rest}>
      {children}
    </a>
  );
}

ExternalLink.propTypes = {
  to: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
};

function SocialLink({ type, ...rest }) {
  const { site } = useStaticQuery(graphql`
    query SocialLinkQuery {
      site {
        siteMetadata {
          social {
            linkedinUrl
            githubUrl
          }
        }
      }
    }
  `);
  const { linkedinUrl, githubUrl } = site.siteMetadata.social;
  let social = {
    text: 'BROKEN',
    url: '',
  };

  switch (type) {
    case 'LinkedIn':
      social = {
        text: type,
        url: linkedinUrl,
      };
      break;
    case 'GitHub':
      social = {
        text: type,
        url: githubUrl,
      };
      break;
    default:
      break;
  }

  return (
    <ExternalLink to={social.url} {...rest}>
      {social.text}
    </ExternalLink>
  );
}

SocialLink.propTypes = {
  type: PropTypes.PropTypes.oneOf(['LinkedIn', 'GitHub']),
};

export function LinkedInLink({ ...rest }) {
  return <SocialLink type="LinkedIn" {...rest} />;
}

export function GitHubLink({ ...rest }) {
  return <SocialLink type="GitHub" {...rest} />;
}
