import React from 'react';
import PropTypes from 'prop-types';
import {
  FaLinkedin as LinkedInIcon,
  FaGithub as GitHubIcon,
} from 'react-icons/fa';
import { ExternalLink } from '../ExternalLink';
import VisuallyHidden from '../VisuallyHidden';

export default function SocialIcons({ social }) {
  const { linkedinUrl, githubUrl } = social;

  return (
    <div className="social-icons">
      <ExternalLink to={linkedinUrl} className="social-icons--link">
        <LinkedInIcon title="LinkedIn" aria-hidden="true" />
        <VisuallyHidden>LinkedIn</VisuallyHidden>
      </ExternalLink>
      <ExternalLink to={githubUrl} className="social-icons--link">
        <GitHubIcon title="GitHub" aria-hidden="true" />
        <VisuallyHidden>GitHub</VisuallyHidden>
      </ExternalLink>
    </div>
  );
}

SocialIcons.propTypes = {
  social: PropTypes.shape({
    linkedinUrl: PropTypes.string.isRequired,
    githubUrl: PropTypes.string.isRequired,
  }),
};
